import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class Shap extends PIXI.Container {
    constructor(color, alpha, width, height) {
        super();

        this._color = color;
        this._alpha = alpha;
        this._width = width;
        this._height = height;

        this.once("added", this.onAddToStage);
    }

    onAddToStage() {
        this._shape = new PIXI.Graphics();
        this._shape.beginFill(this._color, 1);
        this._shape.drawRect(0, 0, this._width, this._height);
        this._shape.endFill();
        this.addChild(this._shape);
        this._shape.alpha = this._alpha;

        this.visible = false;

        this._tween = Tween.TweenMax.to(this._shape, 1,
            {
                paused: true,
                repeat: 1,
                alpha: 1,
                ease: Tween.Sine.easeInOut,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut,
                onComplete: () => {
                    this.visible = false;
                }
            });


    }

    play() {
        this._shape.alpha = 0;
        this.visible = true;

        if (this._tween != null) {
            this._tween.play(0);
        }
    }
}