document.getElementById("setIssue_btn").onclick = (() => {
    main.setIssue(document.getElementById("currissue").value, document.getElementById("preissue").value);
})


document.getElementById("setPreNum_btn").onclick = (() => {
    let numSplit = document.getElementById("setPreNum").value.split(',').map(s => Number(s));
    main.setPreRank(numSplit);
    // document.getElementById("NextTime").value,
    //     document.getElementById("CurrentTime").value)
})


document.getElementById("startCountdown_btn").onclick = (() => {
    main.startCountdown(document.getElementById("startCountdown").value)
})


document.getElementById("midGame_btn").onclick = (() => {
    main.midGame();
})


document.getElementById("finshGame_btn").onclick = (() => {

    let numSplit = document.getElementById("finishGame").value.split(',').map(s => Number(s));
    main.finishGame(numSplit);
})


document.getElementById("randomFinshTest_btn").onclick = (() => {
    main.randomFinshTest();
})



