import * as PIXI from "pixi.js";
import * as Particles from "pixi-particles";

export class Paritcle extends PIXI.Container {

    constructor() {
        super();
        this._emitter = null;

        this._ticker = new PIXI.ticker.Ticker();
        this._ticker.add(() => this.update());

        this.elapsed = Date.now();
        this.visible = false;
    }

    init(data) {

        let textures = [];
        for (let i = 0; i < data.textures.length; i++) {
            textures.push(PIXI.Texture.from(data.textures[i]));
        }

        this._emitter = new Particles.Emitter(this, textures, data.json);
        this._emitter.updateOwnerPos(this.parent._bounds.maxX * 0.45, this.parent._bounds.maxY * 0.3);
    }

    update() {
        let now = Date.now();

        if (this._emitter) {
            this._emitter.update((now - this.elapsed) * 0.001);
        }
        this.elapsed = now;
    }

    play() {
        this._elapsed = Date.now();
        this.visible = true;
        this._ticker.start();
    }

    reset() {
        this.visible = false;
        this._ticker.stop();
    }

}