import * as PIXI from "pixi.js";
import { BrokenNumber } from "./BrokenNumber";

export class Egg extends PIXI.Container {

    init(index, shine_data, egg_tts, crash_tts, shutter_tts, number_tt, lightnumber_tt) {
        this._egg_tts = egg_tts;

        this._egg = PIXI.Sprite.from(this._egg_tts[0]);
        this.addChild(this._egg);

        this._shutter_anima = PIXI.extras.AnimatedSprite.fromFrames(shutter_tts);
        this.addChild(this._shutter_anima);
        this._shutter_anima.anchor.set(0.5);
        this._shutter_anima.x = this.parent.parent.StageWidth * ((index + 1) / 4 - (3 - index) * 0.0025 + (index * 0.0028));
        this._shutter_anima.y = this.parent.parent.StageHeight * 0.565;
        this._shutter_anima.animationSpeed = 0.3;
        this._shutter_anima.loop = false;
        this._shutter_anima.visible = false;
        this._shutter_anima.onComplete = () => { this._shutter_anima.visible = false; };

        this._crashLight_anima = PIXI.extras.AnimatedSprite.fromFrames(crash_tts);
        this.addChild(this._crashLight_anima);
        this._crashLight_anima.scale.set(4);
        this._crashLight_anima.anchor.set(0.5);
        this._crashLight_anima.x = this.parent.parent.StageWidth * ((index + 1) / 4 - (3 - index) * 0.0025 + (index * 0.0028));
        this._crashLight_anima.y = this.parent.parent.StageHeight * 0.5;
        this._crashLight_anima.animationSpeed = 0.3;
        this._crashLight_anima.loop = false;
        this._crashLight_anima.visible = false;
        this._crashLight_anima.onComplete = () => { this._crashLight_anima.visible = false; };

        this._brokenNumberComponent = new BrokenNumber(index, shine_data, number_tt, lightnumber_tt);
        this.addChild(this._brokenNumberComponent);
        this._brokenNumberComponent.x = this.parent.parent.StageWidth * ((index + 1) / 4 + index * 0.01);
        this._brokenNumberComponent.y = this.parent.parent.StageHeight * 0.5;
        this._brokenNumberComponent.init();
    }

    reset() {
        this._egg.texture = this._egg_tts[0];
        this._crashLight_anima.visible = false;
        this._shutter_anima.visible = false;
        this._brokenNumberComponent.reset();
    }

    play() {
        this._egg.texture = this._egg_tts[1];
        this._crashLight_anima.visible = true;
        this._shutter_anima.visible = true;

        this._crashLight_anima.gotoAndPlay(0);
        this._shutter_anima.gotoAndPlay(0);
        this._brokenNumberComponent.play();
    }

    changeBrokenNum(number_tt, lightnumber_tt) {
        this._brokenNumberComponent.setNumberTexture(number_tt, lightnumber_tt);
    }

    get MultitonKey() {
        return this.parent.MultitonKey;
    }
}