import * as PIXI from "pixi.js";

export class Issue extends PIXI.Container {

    init() {
        this._currIssue_te = new PIXI.Text("当期开奖:0期",
            {
                fontSize: 40,
                fill: "#f3ff17",
                fontWeight: "bold",
                letterSpacing: 1,
                strokeThickness: 5
            });

        this._currIssue_te.anchor.set(0, 0.5);
        this.addChild(this._currIssue_te);

        this._currIssue_te.position.set(5, this.parent.StageHeight * 0.06);

        this._preIssue_te = new PIXI.Text("前期开奖:0期",
            {
                fontSize: 30,
                fill: "#f3ff17",
                fontWeight: "bold",
                letterSpacing: 1,
                strokeThickness: 3
            });

        this._preIssue_te.anchor.set(0, 0.5);
        this.addChild(this._preIssue_te);
        this._preIssue_te.position.set(5, this.parent.StageHeight * 0.95);
    }

    setIssue(data) {
        this._currIssue_te.text = `当期开奖:${data.currIssue}期`;
        this._preIssue_te.text = `前期开奖:${data.preIssue}期`;
    }
}