import * as  PIXI from "pixi.js";
import * as Tween from "gsap";
import { BezierTween, BezierScale, BezierPosition, BezierRotation } from "../../../Isaac/Tools/Bezier";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";
import { Paritcle } from "./Paritcle";


export class BrokenNumber extends PIXI.Container {


    constructor(index, shine_data, number_tt, lightnumber_tt) {
        super();

        this._index = index;

        this._orignNum_tt = number_tt;
        this._orignLightNum_tt = lightnumber_tt;

        this._number_sp = new PIXI.Sprite(this._orignNum_tt);
        this.addChild(this._number_sp);

        this._numberlight_sp = new PIXI.Sprite(this._orignLightNum_tt);
        this.addChild(this._numberlight_sp);
        this._numberlight_sp.blendMode = PIXI.BLEND_MODES.ADD;

        this.pivot.set(this.width * 0.5, this.height * 0.5);
        this.scale.set(0.12);
        this.visible = false;

        this._particleComponent = new Paritcle();
        this.addChild(this._particleComponent);
        this._particleComponent.init(shine_data);
    }

    init() {
        this._numberMove = new BezierTween(this,
            [
                {
                    onStart: () => {
                        this._numberlight_sp.alpha = 0.8;
                        Tween.TweenMax.to(this._numberlight_sp, 1.03, { alpha: 0 });
                    },
                    time: 670, delayTime: 0,
                    scale: new BezierScale(new PIXI.Point(0.12, 0.12), new PIXI.Point(0.67, 0.67)),
                    bez: new BezierPosition
                        (
                            new PIXI.Point(this.x, this.y),
                            new PIXI.Point(0, 0),
                            new PIXI.Point(0, -10),
                            new PIXI.Point(this.x - 10, this.y - 200)
                        )
                },
                {
                    time: 500, delayTime: 0,
                    scale: new BezierScale(new PIXI.Point(0.67, 0.67), new PIXI.Point(1.09, 1.09)),
                    rotation: new BezierRotation(0, -0.29),
                    bez: new BezierPosition(
                        new PIXI.Point(this.x - 10, this.y - 200),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(this.x - 15, this.y + 100))
                },
                {
                    onStart: () => { Tween.TweenMax.to(this._numberlight_sp, 0.03, { alpha: 1 }); },
                    time: 60, delayTime: 0,
                    scale: new BezierScale(new PIXI.Point(1.09, 1.09), new PIXI.Point(1.33, 1.33)),
                    rotation: new BezierRotation(-0.29, -0.33)
                },
                {
                    time: 30, delayTime: 0,
                    scale: new BezierScale(new PIXI.Point(1.33, 1.33), new PIXI.Point(1.45, 1.45)),
                    rotation: new BezierRotation(-0.33, -0.257)
                },
                {
                    onStart: () => { Tween.TweenMax.to(this._numberlight_sp, 0.27, { alpha: 0.3 }); },
                    time: 140, delayTime: 0,
                    scale: new BezierScale(new PIXI.Point(1.45, 1.45), new PIXI.Point(1.09, 1.09)),
                    rotation: new BezierRotation(-0.257, 0.36)
                },
                {
                    time: 100, delayTime: 0,
                    onComplete: () => {
                        this.rotation = 0;
                        this._particleComponent.play();
                        if (this._index === 2) {
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameCommandEvent.BROKENCOMPLETE);
                        }
                    },
                    rotation: new BezierRotation(0.36, 0)
                }
            ], false);
    }

    reset() {
        this.visible = false;
        this._numberMove.stop();
        this._number_sp.texture = this._orignNum_tt;
        this._numberlight_sp.texture = this._orignLightNum_tt;

        this._particleComponent.reset();
    }

    play() {
        this.visible = true;
        this._numberMove.play();
    }

    setNumberTexture(number_tt, lightnumber_tt) {
        this._number_sp.texture = number_tt;
        this._numberlight_sp.texture = lightnumber_tt;
    }

}