import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { AudioCommandEvent } from "../../Event/AudioEvent";

export class SoundBtn extends PIXI.Container {

    init(data) {
        this._isAudioPlay = !data.isMuteAll;
        this._enableTexture = PIXI.Texture.from(data.on);
        this._disableTexture = PIXI.Texture.from(data.off);

        this._soundBtn = new PIXI.Sprite(this._isAudioPlay == true ? this._enableTexture : this._disableTexture);
        this._soundBtn.anchor.set(1, 0);
        this.addChild(this._soundBtn);

        this._soundBtn.interactive = true;
        this._soundBtn.buttonMode = true;

        this._soundBtn.on("pointerdown", () => {
            this._isAudioPlay = !this._isAudioPlay;
            this._soundBtn.texture = this._isAudioPlay === true ? this._enableTexture : this._disableTexture;
            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.MUTEALL);
        });

        this.position.set(this.parent.StageWidth * 0.75, 1);
    }

    trigger(isOn) {
        if (isOn !== this._isAudioPlay) {
            this._isAudioPlay = isOn
            this._soundBtn.texture = this._isAudioPlay === true ? this._enableTexture : this._disableTexture;
            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.MUTEALL);
        }

    }
}