import * as  PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameMediator } from "../Mediator/GameMediator";
import { BackGround } from "../Component/BackGround";
import { Hummer } from "../Component/Hummer";
import { EggGroup } from "../Component/EggGroup";
import { GameInfo } from "../Component/GameInfo";
import { Shap } from "../Component/Shap";
import { PreNumber } from "../Component/PreNumber";
import { SoundBtn } from "../Component/SoundBtn";
import { NextIssueInfo } from "../Component/NextIssueInfo";
import { Issue } from "../Component/Issue";


export class GamePanel extends PIXI.Container {



    constructor(data) {
        super();

        this._multitonKey = data.key;
        this._stagewidth = data.width;
        this._stageheight = data.height;


        this.once("added", this.onAddToStage);
    }

    onAddToStage() {
        ApplicationFacade.getInstance(this._multitonKey).registerMediator(new GameMediator(this));

        this._background = new BackGround();
        this.addChild(this._background);
        this._hummer = new Hummer();
        this.addChild(this._hummer);
        this._eggGroup = new EggGroup();
        this.addChild(this._eggGroup);
        this._gameInfo = new GameInfo();
        this.addChild(this._gameInfo);
        // this._nextIssueInfo = new NextIssueInfo();
        // this.addChild(this._nextIssueInfo);

        this._shape = new Shap(0x000000, 0, this._stagewidth, this._stageheight);
        this.addChild(this._shape);
        this._preNumber = new PreNumber();
        this.addChild(this._preNumber);
        this._issue = new Issue();
        this.addChild(this._issue);
        this._soundbtn = new SoundBtn();
        this.addChild(this._soundbtn);
        // this._logo = new PIXI.Sprite();
        // this.addChild(this._logo);

    }

    init(data) {
        this._background.init(data.background);
        this._hummer.init(data.hummer);
        this._eggGroup.init(data.egg);
        this._gameInfo.init();
        this._preNumber.init(data.numframe);
        this._issue.init();
        // this._nextIssueInfo.init(data.infobg);
        // this._logo.texture = PIXI.Texture.from(data.logo);
        // this._logo.position.set(this._stagewidth * 0.01, this._stageheight * 0.01);
        this._soundbtn.init(data.sound);
    }

    reset() {
        this._eggGroup.reset();
        this._hummer.reset();
    }

    get MultitonKey() {
        return this._multitonKey;
    }

    get StageWidth() {
        return this._stagewidth;
    }

    get StageHeight() {
        return this._stageheight;
    }

    get Issue() {
        return this._issue;
    }

    get SoundbtnComponent() {
        return this._soundbtn;
    }

    get GameInfoComponent() {
        return this._gameInfo;
    }

    get HummerComponent() {
        return this._hummer;
    }

    get NextIssueInfoComponet() {
        return this._nextIssueInfo;
    }

    get PreNumberComponent() {
        return this._preNumber;
    }

    get EggGroupComponent() {
        return this._eggGroup;
    }

    get BackgroundComponent() {
        return this._background;
    }

    get ShapeComponent() {
        return this._shape;
    }
}


