import * as PIXI from "pixi.js";
import { Egg } from "./Egg";

export class EggGroup extends PIXI.Container {

    init(data) {

        this._number_tts = [];
        for (let i = 0; i < data.numbers.length; i++) {
            this._number_tts.push(PIXI.Texture.from(data.numbers[i]));
        }

        this._lightNumber_tts = [];
        for (let i = 0; i < data.lightnumbers.length; i++) {
            this._lightNumber_tts.push(PIXI.Texture.from(data.lightnumbers[i]));
        }

        this._egg1 = new Egg();
        this.addChild(this._egg1);
        this._egg1.init(0, data.shine, [PIXI.Texture.from(data.e1[0]), PIXI.Texture.from(data.e1[1])]
            , data.crashlights, data.shutters, this._number_tts[0], this._lightNumber_tts[0]);

        this._egg2 = new Egg();
        this.addChild(this._egg2);
        this._egg2.init(1, data.shine, [PIXI.Texture.from(data.e2[0]), PIXI.Texture.from(data.e2[1])]
            , data.crashlights, data.shutters, this._number_tts[0], this._lightNumber_tts[0]);

        this._egg3 = new Egg();
        this.addChild(this._egg3);
        this._egg3.init(2, data.shine, [PIXI.Texture.from(data.e3[0]), PIXI.Texture.from(data.e3[1])]
            , data.crashlights, data.shutters, this._number_tts[0], this._lightNumber_tts[0]);
    }

    reset() {
        this._egg1.reset();
        this._egg2.reset();
        this._egg3.reset();
    }

    changeBrokenNum(numbers) {
        this._egg1.changeBrokenNum(this._number_tts[numbers[0]], this._lightNumber_tts[numbers[0]]);
        this._egg2.changeBrokenNum(this._number_tts[numbers[1]], this._lightNumber_tts[numbers[1]]);
        this._egg3.changeBrokenNum(this._number_tts[numbers[2]], this._lightNumber_tts[numbers[2]]);
    }

    broken(index) {
        switch (index) {
            case 0:
                this._egg1.play();
                break;
            case 1:
                this._egg2.play();
                break;
            case 2:
                this._egg3.play();
                break;
            default:
                break;
        }
    }

    get MultitonKey() {
        return this.parent.MultitonKey;
    }
}
