import * as PIXI from "pixi.js";
import { Panel } from "../Common/Panel";
import { GamePanel } from "../View/Panel/GamePanel";

export class ApplicationMain extends PIXI.Container {
    constructor() {
        super();

        this._currPanels = [];

        //_soundbtn = null;
        this._soundOnOff = true;
        this._sound_tes = {};

        this._data;
    }

    loadPanel(name) {
        if (this._currPanels != null) {
            this._currPanels.forEach(panel => {
                this.removeChild(panel);
            });

            this._currPanels.length = 0;
        }

        switch (name) {
            case Panel.GamePanel:
                this._currPanels.push(new GamePanel(this._data));
                break;
            default:
                break;
        }


        for (let i = 0; i < this._currPanels.length; i++) {
            this.addChild(this._currPanels[i]);
        }
    }

    setData(width, height, key) {
        this._data = {
            width: width,
            height: height,
            key: key
        };
    }

}