import * as PIXI from "pixi.js";
import { BezierTween, BezierPosition } from "../../../Isaac/Tools/Bezier";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { ApplicationFacadeEvent } from "../../Event/ApplicationFacadeEvent";
import { GameEvent } from "../../Event/GameEvent";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../../Mode/Data/AudioManifest";

export class Hummer extends PIXI.Container {


    constructor() {
        super();

        this._leftHitPoint = new PIXI.Point(0.39, 1);
        this._midHitPoint = new PIXI.Point(0.90, 1);
        this._rightHitPoint = new PIXI.Point(1.43, 1);
    }

    init(data) {
        this._centerPos = new PIXI.Point(this.parent.StageWidth * 0.5, this.parent.StageHeight * 0.5);

        this._hummerAnimation = PIXI.extras.AnimatedSprite.fromFrames(data);
        this.addChild(this._hummerAnimation);
        this._hummerAnimation.loop = false;
        this._hummerAnimation.animationSpeed = 0.25;
        this._hummerAnimation.anchor.set(0.5);
        this._hummerAnimation.position.set(this._centerPos.x * 2.5, this._centerPos.y);
        this._hummerAnimation.visible = false;


        this._moveIn = new BezierTween(this._hummerAnimation,
            [
                {
                    time: 5000, delayTime: 0,
                    bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 2.5, this._centerPos.y * 0.98),
                        new PIXI.Point(-200, -100),
                        new PIXI.Point(200, 100),
                        new PIXI.Point(this._centerPos.x * 1.48, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 1.48, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 0.44, this._centerPos.y * 0.98))
                },
            ], false, false, null, () => { this._moveLoop.play(); });

        this._moveLoop = new BezierTween(this._hummerAnimation,
            [
                {
                    time: 1500, delayTime: 50, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.44, this._centerPos.y * 0.98),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98)),
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 1.48, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 1.48, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.48 - this._centerPos.x * 1.21) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 1.21, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 1.21 - this._centerPos.x * 0.95) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 0, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.95, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.95 - this._centerPos.x * 0.69) / 4, -25),
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98))
                },
                {
                    time: 1500, delayTime: 50, bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * 0.69, this._centerPos.y * 0.98),
                        new PIXI.Point(-Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(Math.abs(this._centerPos.x * 0.69 - this._centerPos.x * 0.44) / 4, 25),
                        new PIXI.Point(this._centerPos.x * 0.44, this._centerPos.y * 0.98))
                },
            ], true, false)
    }

    reset() {
        this._moveIn.stop();
        this._moveLoop.stop();
        this._moveHit.stop();

        this._hummerAnimation.position.set(this._centerPos.x * 2.5, this._centerPos.y);
        this._hummerAnimation.visible = false;
    }

    moveIn() {
        this._hummerAnimation.visible = true;
        this._moveIn.play();
    }

    moveMid() {
        this._hummerAnimation.visible = true;
        this._moveIn.stop();
        this._moveLoop.play();
    }

    moveHit() {
        this._hummerAnimation.visible = true;
        this._moveIn.stop();
        this._moveLoop.stop();

        this._moveHit = new BezierTween(this._hummerAnimation,
            [
                {
                    time: 1000, delayTime: 0,
                    onComplete: () => {
                        this._hummerAnimation.gotoAndPlay(0);


                        setTimeout(() => {
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameEvent.BROKENEGG, 0);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(ApplicationFacadeEvent.SHOCKMAIN, 2);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.NumberFlash });
                        }, 250);
                    },
                    bez: new BezierPosition(
                        new PIXI.Point(this._hummerAnimation.x, this._hummerAnimation.y),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(this._centerPos.x * this._leftHitPoint.x, this._centerPos.y * this._leftHitPoint.y)
                    )
                },
                {
                    time: 100, delayTime: 250,
                    onComplete: () => {
                        this._hummerAnimation.gotoAndPlay(0);

                        setTimeout(() => {
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameEvent.BROKENEGG, 1);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(ApplicationFacadeEvent.SHOCKMAIN, 2);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.NumberFlash });
                        }, 250);
                    },
                    bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * this._leftHitPoint.x, this._centerPos.y * this._leftHitPoint.y),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(this._centerPos.x * this._midHitPoint.x, this._centerPos.y * this._midHitPoint.y)
                    )
                },
                {
                    time: 100, delayTime: 250,
                    onComplete: () => {
                        this._hummerAnimation.gotoAndPlay(0);

                        setTimeout(() => {
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameEvent.BROKENEGG, 2);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(ApplicationFacadeEvent.SHOCKMAIN, 2);
                            ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.NumberFlash });
                            this._hummerAnimation.visible = false;
                        }, 250);
                    },
                    bez: new BezierPosition(
                        new PIXI.Point(this._centerPos.x * this._midHitPoint.x, this._centerPos.y * this._midHitPoint.y),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(0, 0),
                        new PIXI.Point(this._centerPos.x * this._rightHitPoint.x, this._centerPos.y * this._rightHitPoint.y)
                    )
                }
            ], false);

        this._moveHit.play();
    }

}