import MVC from "puremvc";
import { GameProxyEvent, GameEvent } from "../../Event/GameEvent";

export class GameMediator extends MVC.Mediator {

    static get NAME() {
        return "GameMediator";
    }

    constructor(viewComponent) {
        super(GameMediator.NAME, viewComponent);
    }

    listNotificationInterests() {
        return [
            GameProxyEvent.RESET,
            GameProxyEvent.INIT,
            GameProxyEvent.SETISSUE,
            GameProxyEvent.SETAUIDOBTN,
            GameProxyEvent.UPDATEGAMEINFO,
            GameProxyEvent.HUMMERIN,
            GameProxyEvent.UPPRENUM,
            GameProxyEvent.UPPREISSUEINFO,
            GameProxyEvent.UPBROKENNUM,
            GameProxyEvent.HITEGG,
            GameProxyEvent.SHOWSHAPEMASK,
            GameEvent.BROKENEGG,
        ];
    }

    handleNotification(notification) {
        switch (notification.getName()) {
            case GameProxyEvent.RESET:
                this.viewComponent.reset();
                break;
            case GameProxyEvent.INIT:
                this.viewComponent.init(notification.getBody());
                break;
            case GameProxyEvent.SETISSUE:
                this.viewComponent.Issue.setIssue(notification.getBody());
                break;
            case GameProxyEvent.SETAUIDOBTN:
                this.viewComponent.SoundbtnComponent.trigger(notification.getBody());
                break;
            case GameProxyEvent.UPDATEGAMEINFO:
                this.viewComponent.BackgroundComponent.upGameInfo(notification.getBody());
                this.viewComponent.GameInfoComponent.upGameInfo(notification.getBody());
                break;
            case GameProxyEvent.HUMMERIN:
                this.viewComponent.HummerComponent.moveIn();
                break;
            case GameProxyEvent.UPPRENUM:
                this.viewComponent.PreNumberComponent.setNum(notification.getBody());
                break;
            case GameProxyEvent.UPPREISSUEINFO:
                // this.viewComponent.NextIssueInfoComponet.updata(notification.getBody());
                break;
            case GameProxyEvent.UPBROKENNUM:
                this.viewComponent.EggGroupComponent.changeBrokenNum(notification.getBody());
                break;
            case GameProxyEvent.HITEGG:
                this.viewComponent.HummerComponent.moveHit();
                break;
            case GameProxyEvent.SHOWSHAPEMASK:
                this.viewComponent.ShapeComponent.play();
                break;
            case GameEvent.BROKENEGG:
                this.viewComponent.EggGroupComponent.broken(notification.getBody());
                break;
            default:
                break;
        }
    }
}

