import MVC from "puremvc";
import * as PIXI from "pixi.js";
import { GameProxyEvent } from "../../Event/GameEvent";
import { GameState } from "../../Common/GameState";
import { AudioCommandEvent } from "../../Event/AudioEvent";
import { AudioManifest } from "../Data/AudioManifest";
import * as Tween from "gsap";
import { ApplicationFacadeEvent } from "../../Event/ApplicationFacadeEvent";
import { Tool } from "../../../Tools/Tool";

export class GameProxy extends MVC.Proxy {

    static get NAME() {
        return "GameProxy";
    }

    constructor(key) {
        super(GameProxy.NAME);

        this.initializeNotifier(key);

        this._ticker;
        this._currCountTimer = 0;

        this._isStartCountDown = false;
        this._isStartGame = false;
        this._isFinshGame = false;

        this._currIssue = 0;
        this._preIssue = 0;

        this._nextTime = "00:00:00";
        this._currTime = "00:00:00";
        this._currNum = [];
        this._preNum = [];


        this._soundVol = 1;

        this._currGameState = "";
    }

    remove() {
    }

    onRemove() {
        this._isRemvoe = true;
        this._ticker.destroy();
    }

    init(data) {

        this.sendNotification(ApplicationFacadeEvent.LOADPANEL, "GamePanel");

        this._currGameState = GameState.init;
        this.onReady = data.onReady;
        this.onStartGame = data.onStartGame;
        this.onFinshGame = data.onFinshGame;

        this._ticker = new PIXI.ticker.Ticker();
        this._ticker.add(() => this.update());


        let crashLights = [];
        for (let i = 0; i < 9; i++) {
            crashLights.push(Tool.Format("CrashLight_{0}.png", Tool.PadLeft((i + 1), "2", "0")));
        }

        let shutters = [];
        for (let i = 0; i < 11; i++) {
            shutters.push(Tool.Format("shutter_{0}.png", Tool.PadLeft((i + 1), "2", "0")));
        }

        let numbers = [];
        let lightnumbers = [];
        for (let i = 0; i < 10; i++) {
            numbers.push(Tool.Format("{0}.png", Tool.PadLeft(i, "2", "0")));
            lightnumbers.push(Tool.Format("{0}_light.png", Tool.PadLeft(i, "2", "0")));
        }



        this.sendNotification(GameProxyEvent.INIT,
            {
                background: { "bg": "Background.jpg", "lightbg": "backlight.jpg" },
                hummer: ["槌_01.png", "槌_02.png", "槌_03.png"],
                egg:
                {
                    e1: ["egg1_1.png", "egg1_2.png"],
                    e2: ["egg2_1.png", "egg2_2.png"],
                    e3: ["egg3_1.png", "egg3_2.png"],
                    crashlights: crashLights,
                    shutters: shutters,
                    numbers: numbers,
                    lightnumbers: lightnumbers,
                    shine: { json: data.loader.resources['particle'].data, textures: ["shine"] }
                },
                logo: "ChinaLogo.png",
                numframe: "num_frame.png",
                infobg: "info_BG.png",
                sound: { isMuteAll: data.isMuteAll, on: "sound_on.png", off: "sound_off.png" }
            });

        this.setPreRank(data.preNum);

        this._currGameState = GameState.stop;
        this.setIssue({ currIssue: data.currIssue, preIssue: data.preIssue });
        this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState });
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.BitAndSeal, loop: true, plural: false });

        if (this.onReady !== undefined && this.onReady !== null) {
            setTimeout(() => {
                this.onReady();
            }, 500);
        }
    }

    reset() {
        this._currGameState = GameState.stop;
        this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState });
        this.sendNotification(GameProxyEvent.RESET);

        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.BitAndSeal, loop: true, plural: false });

        Tween.TweenMax.to(this, 1,
            {
                SoundVol: 1,
                ease: Tween.Sine.easeInOut,
                onUpdate: () => {
                    this.sendNotification(AudioCommandEvent.VOLUMEALL, this.SoundVol);
                },
            })

        this._isFinshGame = false;
        this._isStartGame = false;
        this._isStartCountDown = false;
    }

    update() {
        if (this._isStartCountDown) {
            let mathTime = Math.floor(this._currCountTimer);
            if (mathTime > 0) {
                this._currCountTimer -= this._ticker.elapsedMS / 1000;
                this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState, "time": mathTime });
            }
            else {
                this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.StateChange });

                setTimeout(() => {
                    this.startGame();
                }, 500);

                this._isStartCountDown = false;
            }
        }
    }

    startGame() {
        this._ticker.stop();
        this._isStartGame = true;

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.BitAndSeal);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.Lotterying, loop: true, plural: false });

        this._currGameState = GameState.sealplate;
        this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState });
        this.sendNotification(GameProxyEvent.HUMMERIN);

        if (this.onStartGame !== undefined && this.onStartGame !== null)
            this.onStartGame();
    }

    getState(fn) {
        fn(this._currGameState);
    }

    setReadyCallBack(fn) {
        this._readyCallBack = fn;
    }

    setIssue(data) {
        this._currIssue = data.currIssue;
        this._preIssue = data.preIssue;
        this.sendNotification(GameProxyEvent.SETISSUE, data);
    }

    setPreInfo(data) {
        this._preNum = data["numbers"];
        this._nextTime = data["nexttime"];
        this._currTime = data["currtime"];

        //this._preIssue = data[ "issue" ];
        // this.sendNotification( GameProxyEvent.UPISSUE, this._preIssue );

        this.sendNotification(GameProxyEvent.UPPRENUM, this._preNum);
        this.sendNotification(GameProxyEvent.UPPREISSUEINFO, { "nextTime": this._nextTime, "currTime": this._currTime });
    }


    setPreRank(data) {
        this._preNum = data;
        this.sendNotification(GameProxyEvent.UPPRENUM, this._preNum);
    }

    startCountDown(second) {
        if (!this._isStartGame && !this._isFinshGame) {
            this._currCountTimer = second;
            this._isStartCountDown = true;

            this._currGameState = GameState.counttime;
            this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState, "time": this._currCountTimer });

            if (!this._ticker.started) {
                this._ticker.start();
            }
        }
    }

    midGame() {
        if (!this._isStartGame && !this._isFinshGame) {
            if (!this._ticker.started) {
                this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.StateChange });

                this._ticker.start();
            }

            this.startGame();
        }
    }

    finshGame(data) {
        if (this._isFinshGame) return;

        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.BitAndSeal);
        this.sendNotification(AudioCommandEvent.STOP, AudioManifest.Lotterying);
        this.sendNotification(AudioCommandEvent.PLAY, { key: AudioManifest.Lottery, plural: false });

        this._ticker.stop();
        this._isFinshGame = true;
        this._isStartGame = true;
        this._isStartCountDown = false;

        this._currNum = data;

        this._currGameState = GameState.opening;
        this.sendNotification(GameProxyEvent.UPDATEGAMEINFO, { "state": this._currGameState });
        this.sendNotification(GameProxyEvent.UPBROKENNUM, this._currNum);
        this.sendNotification(GameProxyEvent.HITEGG);
    }

    onbrokenComplete() {
        setTimeout(() => {
            Tween.TweenMax.to(this, 2,
                {
                    SoundVol: 0,
                    ease: Tween.Sine.easeInOut,
                    onUpdate: () => {
                        this.sendNotification(AudioCommandEvent.VOLUMEALL, this.SoundVol);
                    },
                    onComplete: () => {
                        this.sendNotification(GameProxyEvent.SHOWSHAPEMASK);

                        // let temp = this._currIssue;
                        // this.setIssue({ currIssue: Number(this._currIssue) + 1, preIssue: temp });

                        this._preNum = this._currNum;
                        this.sendNotification(GameProxyEvent.UPPRENUM, this._preNum);

                        let newNextTime = this.conventNumberTimeToString(this.conventStringTimeToNumber(this._nextTime) + 30 * 60);
                        this._currTime = this._nextTime;
                        this._nextTime = newNextTime;
                        this.sendNotification(GameProxyEvent.UPPREISSUEINFO, { "nextTime": this._nextTime, "currTime": this._currTime });

                        setTimeout(() => {
                            if (this.onFinshGame !== null && this.onFinshGame !== undefined) {
                                this.onFinshGame();
                            }
                            this.reset()
                        }, 1000);
                    }
                })
        }, 7000);
    }

    conventStringTimeToNumber(time) {
        let splitTime = time.split(':');
        let hh = Number(splitTime[0]);
        let mm = Number(splitTime[1]);
        let ss = Number(splitTime[2]);
        return hh * 60 * 60 + mm * 60 + ss;
    }

    conventNumberTimeToString(num) {
        let h = Tool.PadLeft(Math.floor((num / 60) / 60), 2, "0");
        let m = Tool.PadLeft(Math.floor((num / 60) % 60), 2, "0");
        let s = Tool.PadLeft(num % 60, 2, "0");
        return h + ":" + m + ":" + s
    }

    get SoundVol() {
        return this._soundVol;
    }

    set SoundVol(v) {
        this._soundVol = v;
    }

    sendNotification(key, data) {
        if (!this._isRemvoe) {
            super.sendNotification(key, data)
        }
    }
}
