import * as PIXI from "pixi.js";
import { Shap } from "./Shap";

export class PreNumber extends PIXI.Container {

    init(frame_tt) {

        this.frame = new Shap(0x000000, 0.5, this.parent.StageWidth, this.parent.StageHeight * 0.1);
        this.frame.position.y = this.parent.StageHeight * 0.9;
        this.addChild(this.frame);
        this.frame.visible = true;

        this._numframe1 = PIXI.Sprite.from(frame_tt);
        this._numframe1.anchor.set(0.5);
        this.addChild(this._numframe1);
        this._numframe1.position.set(this.parent.StageWidth * 0.35, this.parent.StageHeight * 0.95);

        this._numframe2 = PIXI.Sprite.from(frame_tt);
        this._numframe2.anchor.set(0.5);
        this.addChild(this._numframe2);
        this._numframe2.position.set(this.parent.StageWidth * 0.5, this.parent.StageHeight * 0.95);

        this._numframe3 = PIXI.Sprite.from(frame_tt);
        this._numframe3.anchor.set(0.5);
        this.addChild(this._numframe3);
        this._numframe3.position.set(this.parent.StageWidth * 0.65, this.parent.StageHeight * 0.95);

        this._num1 = new PIXI.extras.BitmapText("0", { font: '50px Numberfnt' })
        this.addChild(this._num1);
        this._num1.anchor.set(0.5);
        this._num1.position.set(this._numframe1.x, this._numframe1.y);

        this._num2 = new PIXI.extras.BitmapText("0", { font: '50px Numberfnt' })
        this.addChild(this._num2);
        this._num2.anchor.set(0.5);
        this._num2.position.set(this._numframe2.x, this._numframe2.y);

        this._num3 = new PIXI.extras.BitmapText("0", { font: '50px Numberfnt' })
        this.addChild(this._num3);
        this._num3.anchor.set(0.5);
        this._num3.position.set(this._numframe3.x, this._numframe3.y);
    }

    setNum(numbers) {
        if (Array.isArray(numbers)) {
            if (numbers.length < 3)
                console.error("Set Text Number Error");

            this._num1.text = numbers[0];
            this._num2.text = numbers[1];
            this._num3.text = numbers[2];
        }
    }
} 