import * as PIXI from "pixi.js";
import * as Tween from "gsap";
import { GameState } from "../../Common/GameState";

export class BackGround extends PIXI.Container {

    init(data) {
        this._background = PIXI.Sprite.from(data.bg);
        this.addChild(this._background);

        this._lightBackgroud = PIXI.Sprite.from(data.lightbg);
        this.addChild(this._lightBackgroud);
        this._lightBackgroud.blendMode = PIXI.BLEND_MODES.ADD;
        this._lightBackgroud.visible = false;

        this._lightTween = Tween.TweenMax.to(this._lightBackgroud, 1,
            {
                alpha: 0,
                ease: Tween.Sine.easeInOut,
                onComplete: () => {
                    this._lightBackgroud.visible = false;
                    this._lightBackgroud.alpha = 1;
                }
            });

    }

    upGameInfo(data) {
        if (data["state"] !== GameState.counttime) {
            this._lightBackgroud.visible = true;
            this._lightTween.play(0);
        }
    }
}