import * as PIXI from "pixi.js";
import { Tool } from "../../../Tools/Tool";
import { GameState } from "../../Common/GameState";

export class GameInfo extends PIXI.Container {

    init() {
        this._gameInfo_te = new PIXI.extras.BitmapText("倒计时 : 00:00:00", { font: '70px customfnt1' })
        this.addChild(this._gameInfo_te);
        this._gameInfo_te.anchor.set(0.5);
        this._gameInfo_te.position.set(this.parent.StageWidth * 0.5, this.parent.StageHeight * 0.3);
    }

    upGameInfo(data) {
        if (data["state"] !== this._currGameState) {
            switch (data["state"]) {
                case GameState.stop:
                case GameState.init:
                    this.visible = false;
                    break;
                default:
                    this.visible = true;
                    break;
            }
            this._currGameState = data["state"];
        }


        switch (data["state"]) {
            case GameState.counttime:
                this._currGameState = data["state"];
                this._gameInfo_te.text = "倒计时:" + this.conventToTime(data["time"]);
                break;
            case GameState.sealplate:
                this._gameInfo_te.text = "封盘中";
                break;
            case GameState.opening:
                this._gameInfo_te.text = "开奖中";
                break;
            default:
                break;
        }
    }

    conventToTime(num) {
        let h = Tool.PadLeft(Math.floor((num / 60) / 60), 2, "0");
        let m = Tool.PadLeft(Math.floor((num / 60) % 60), 2, "0");
        let s = Tool.PadLeft(num % 60, 2, "0");
        return h + ":" + m + ":" + s
    }
}